@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}#html{position:relative}body{background-color:#ffc8c88a}


header{width:100%;background-image:repeating-linear-gradient(45deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(90deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(0deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(135deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),linear-gradient(90deg, #444f5a, #444f5a);height:200px;text-align:center;vertical-align:middle}header h1{color:#ffc8c8;line-height:200px;font-size:4rem;font-family:"Pacifico", cursive}header ul{list-style:none}header ul li{display:inline-block;margin-right:15px}

footer{width:100%;background-color:#444f5a;height:40px;text-align:center;vertical-align:middle;position:absolute;left:0;bottom:0;right:0}footer p{color:#ffc8c8;line-height:2rem;font-size:1rem;font-family:"Pacifico", cursive}footer a{text-decoration:none;color:#f99}.footer-true{position:relative}

main{width:100%;text-align:center}main div{padding:10px;background-color:#444f5a;width:60%;margin:20px auto;border-radius:15px;box-shadow:5px 5px #f99}main div form{margin-bottom:13px}main div form button{width:50px;height:25px;background-color:#f99;border-radius:5px;font-weight:600;color:#3e4149}main div form input[type="url"]{margin-left:25px;margin-bottom:15px;margin:10px 0px 25px;width:40%}main div form label{margin-right:15px;font-weight:600;font-family:"Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;color:#f99}main div form textarea{display:block;margin:10px auto 5px auto;width:80%}

table{font-family:"Trebuchet MS", Arial, Helvetica, sans-serif;border-collapse:collapse;width:63%;margin:0 auto 20px}table td,table th{padding:8px;border-radius:15px;border:5px solid #ffe1e1;text-align:center}table tr:hover td{background-color:#ddd}table td{cursor:pointer;background-color:#f2f2f2}table th{padding-top:12px;padding-bottom:12px;padding-left:12px;text-align:left;background-color:#ff9999;color:white}

.json-div #json-pretty{background-image:repeating-linear-gradient(45deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(90deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(0deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),repeating-linear-gradient(135deg, rgba(222,235,40,0.05) 0px, rgba(222,235,40,0.05) 1px, transparent 1px, transparent 11px, rgba(222,235,40,0.05) 11px, rgba(222,235,40,0.05) 12px, transparent 12px, transparent 32px),linear-gradient(90deg, #444f5a, #444f5a);border-radius:15px;width:57%;margin:0 auto;box-shadow:5px 5px #f99;padding:30px;margin-bottom:20px}.json-div .__json-pretty__{padding-left:10px;line-height:1.3;color:#748096;background:#fff;height:500px;overflow:scroll}.json-div .__json-pretty__ .__json-key__{color:#3e4149}.json-div .__json-pretty__ .__json-value__{color:#f99;width:80%}.json-div .__json-pretty__ .__json-string__{color:#f99;width:80%}.json-div .__json-pretty__ .__json-boolean__{color:#448aa9;width:80%}.json-div .__json-pretty-error__{text-align:center;margin:0 auto;line-height:1.3;color:#3e4149;background:white;width:83%;padding:5px}

